.btn,
.btn:hover,
.btn:focus {
  display: inline-block;
  padding: 12px 20px;
  border: 1px solid transparent;
  background: #ffffff;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
  color: #30D5C8;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  overflow: hidden;
  margin: 5px;
  text-align: center;
  font-family: 'OpenSans', sans-serif;
  font-weight: normal;
  font-style: normal;
}
.btn .icon,
.btn:hover .icon,
.btn:focus .icon {
  margin-left: 0 5px;
}
.btn.btn-lg,
.btn:hover.btn-lg,
.btn:focus.btn-lg {
  padding: 18px 25px;
}
.btn.btn-sm,
.btn:hover.btn-sm,
.btn:focus.btn-sm {
  padding: 5px 5px;
}
.btn.btn-primary,
.btn:hover.btn-primary,
.btn:focus.btn-primary {
  color: #FFFFFF;
  background-color: #30D5C8;
}
.btn.btn-no-border,
.btn:hover.btn-no-border,
.btn:focus.btn-no-border {
  border: 1px solid transparent;
}
.icon {
  background-repeat: no-repeat;
  display: inline-block;
}
.icon.icon-close {
  background-image: url(data:image/svg+xml;base64,);
  height: 16px;
  width: 16px;
}
