.light() {
	font-family: 'Open Sans', sans-serif;
	font-weight: 300;
	font-style: normal;
}

.regular() {
	font-family: 'OpenSans', sans-serif;
	font-weight: normal;
	font-style: normal;
}

.bold() {
	font-family: 'Open Sans', sans-serif;
	font-weight: bold;
	font-style: normal;
}

